// --- orientation -------------------------------------------------------------
@mixin _orientation($orientation) {
  @media screen and (orientation: $orientation) {
    @content;
  }
}

@mixin landscape {
  @include _orientation(landscape) {
    @content;
  }
}

@mixin portrait {
  @include _orientation(portrait) {
    @content;
  }
}